section {
  scroll-margin-top: 100px;
}

span {
  color: #38bdf8;
}

/** Media Queries **/
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.footer {
  padding: 2rem 0 0 0;
  text-align: center;
}
.footer .footer__info {
  justify-content: space-around;
}
@media (min-width: 400px) {
  .footer .footer__info {
    display: flex;
  }
}
.footer .info {
  text-align: left;
}
.footer .contactos {
  padding: 2rem;
}
.footer .contactos a {
  display: flex;
  text-decoration: none;
}
.footer .contactos a i {
  padding-top: 1rem;
  padding-right: 1rem;
}
.footer .contactos a p {
  color: white;
}